.footer {
  background-color: #000 !important;
  position: relative;
  bottom: 0;

  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #fff;

  .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
  .img {
    img {
      width: 2rem;
      height: 2rem;
      border-radius: 5px;
    }
  }
}
